import React from 'react';
import {lang_gemoProds} from '../data/lang';

import '../styles/css/gemoProds.css';

class GemoProds extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      currPlate: 5,
    };

    this.plateWidth = 15;
    this.plateHeight = 22.5;
  }

  setPlateStyle = (plateIndex, length) => {
    let plateStyle = {};

    if (plateIndex + 1 < this.state.currPlate) {
      plateStyle.left = `${(plateIndex - 1) * (this.plateWidth * 0.2)}vw`;
      plateStyle.transform = `rotateY(60deg)`;
      plateStyle.zIndex = `${plateIndex + 1}`;
      plateStyle.boxShadow = '-0.74vw 0.37vw 0.37vw 0 #00000077';
    }

    if (plateIndex + 1 === this.state.currPlate) {
      plateStyle.left = `${plateIndex * (this.plateWidth * 0.2) + (this.plateWidth * 0.425)}vw`;
      plateStyle.zIndex = `${length}`;
    }

    if (plateIndex + 1 > this.state.currPlate) {
      plateStyle.left = `${plateIndex * (this.plateWidth * 0.2) + (this.plateWidth * 1.05)}vw`;
      plateStyle.transform = `rotateY(-60deg)`;
      plateStyle.zIndex = `${length - plateIndex}`;
      plateStyle.boxShadow = '0.74vw 0.37vw 0.37vw 0 #00000077';
    }

    return plateStyle;
  }

  render() {
    const prods = lang_gemoProds.en;
    return (
      <div 
        className="platesWrap"
        style={{width: `${(prods.packages.length - 3) * (this.plateWidth * 0.2) + (this.plateWidth * 2.25)}vw`, height: `${this.plateHeight * 1.3}vw`}}
      > 
        {prods.packages.map((product, index) => (
          <div 
            key={product.title}
            onMouseOver={() => this.setState({ currPlate: index+1 })}
            className="plate"
            style={this.setPlateStyle(index, prods.length)}
          >
            <div 
              className="plate__inner"
              style={{backgroundImage: `url(${product.background})`}}
            >
              <div className="package">
                <h3 className="package__title">{product.title}</h3>
                <p className="package__includesTitle">{prods.package_includes_title}</p>
                <ul className="package__includesList">
                  {product.package_includes.map(item => (
                    <li key={item} className="package__includesItem">
                      {item}
                    </li>
                  ))}
                </ul>
                <div className="package__price price">
                  <p className="price__title">{prods.price_title}</p>
                  <p className="price__value">{product.price}</p>
                  <p className="price__currency">{prods.currancy}</p>
                </div>
                <div className="package__period period">
                  <p className="period__title">{prods.period_title}</p>
                  <p className="period__time">{product.period}</p>
                  <p className="period__value">{prods.period_value}</p>
                </div>
              </div>
            </div>
          </div>
        ))}
      
        <div className="platesWrap__shaddow" />
      </div>
    );
  }
}

export default GemoProds;

